.img {
  width: 500px;
  height: auto;
  
}

.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
  margin: 20px auto;
}